.lume-image-linking__image {
  width: 100%;
  height: 100%;
}
.lume-image-linking__image-holder {
  position: relative;
}
.lume-image-linking__dot {
  position: absolute;
  width: 30px;
  height: 30px;
  background-color: #233C29;
  border-radius: 50%;
  box-shadow: 0 0 0 10px rgba(35, 60, 41, 0.29);
  cursor: pointer;
  animation: pulse 2s infinite;
}
.lume-image-linking__dot:active, .lume-image-linking__dot:focus, .lume-image-linking__dot:hover {
  animation: none;
}
.lume-image-linking__dot--1, .lume-image-linking__dot--3 {
  animation: pulse 3s infinite;
}
.lume-image-linking__dot--2, .lume-image-linking__dot--4 {
  animation: pulse 4s infinite;
}
.lume-image-linking .dot-content {
  position: absolute;
  top: calc(100% + 20px);
  transform: translateX(-50%);
  display: none;
  left: 50%;
  padding: 0 15px;
  width: max-content;
  height: 43px;
  align-items: center;
  justify-content: center;
  font-size: 15px;
  color: #f1ebdb !important;
  text-decoration: none;
  text-align: center;
  background-color: #233C29;
  border-radius: 50px;
}
.lume-image-linking .dot-content:before {
  content: "";
  background-image: url(../../assets/images/linkedin.svg);
  background-repeat: no-repeat;
  display: inline-block;
  width: 25px;
  height: 22px;
  vertical-align: bottom;
  margin-bottom: 0px;
}
.lume-image-linking .dot-content:after {
  content: "";
  position: absolute;
  top: 0;
  width: 20px;
  height: 20px;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  background-color: #233C29;
}
@media screen and (min-width: 992px) {
  .lume-image-linking {
    padding-right: 65px;
  }
}
@media screen and (max-width: 991px) {
  .lume-image-linking__image-holder {
    margin-bottom: 2rem;
  }
}
@media screen and (max-width: 767px) {
  .lume-image-linking__dot {
    box-shadow: 0 0 0 5px rgba(35, 60, 41, 0.29);
  }
  .lume-image-linking__dot--1 {
    left: 27% !important;
  }
  .lume-image-linking__dot--4 {
    left: 70% !important;
  }
  .lume-image-linking .dot-content {
    top: calc(100% + 12px);
  }
  .lume-image-linking .dot-content:after {
    width: 10px;
    height: 10px;
  }
}
@media (min-width: 576px) and (max-width: 767px) {
  .lume-image-linking__dot {
    width: 20px;
    height: 20px;
  }
  .lume-image-linking__dot--3 {
    left: 70% !important;
  }
}
@media screen and (max-width: 575px) {
  .lume-image-linking__dot {
    width: 15px;
    height: 15px;
  }
  .lume-image-linking__dot--3 {
    left: 60% !important;
  }
  .lume-image-linking .dot-content {
    padding: 8px 10px;
    font-size: 0.7rem;
  }
}

@-webkit-keyframes pulse {
  0% {
    -webkit-box-shadow: 0 0 0 0 rgba(35, 60, 41, 0.29);
  }
  70% {
    -webkit-box-shadow: 0 0 0 10px rgba(35, 60, 41, 0.29);
  }
  100% {
    -webkit-box-shadow: 0 0 0 0 rgba(35, 60, 41, 0.29);
  }
}
@keyframes pulse {
  0% {
    -moz-box-shadow: 0 0 0 0 rgba(35, 60, 41, 0.29);
    box-shadow: 0 0 0 0 rgba(35, 60, 41, 0.29);
  }
  70% {
    -moz-box-shadow: 0 0 0 10px rgba(35, 60, 41, 0.29);
    box-shadow: 0 0 0 10px rgba(35, 60, 41, 0.29);
  }
  100% {
    -moz-box-shadow: 0 0 0 0 rgba(35, 60, 41, 0.29);
    box-shadow: 0 0 0 0 rgba(35, 60, 41, 0.29);
  }
}